import axios from "axios";
import { baseReadURL, baseWriteURL } from "../../package.json";

function readFile(file) {
  return axios
    .get(baseReadURL + `${file}` + "?ref=data")
    .then((response) => {
      return response.data;
    })
    .catch((error) => error.response);
}

function getShaFile(file) {
  return axios
    .get(baseWriteURL + `${file}` + "?ref=data")
    .then((response) => {
      return response.data;
    })
    .catch((error) => error.response);
}

